import React, { useState } from 'react';
import { 
  AppBar, 
  Toolbar, 
  Typography, 
  Container, 
  Paper, 
  Tabs, 
  Tab,
  Box,
  TextField,
  Button,
  Typography as MuiTypography,
  Grid,
  Card,
  CardContent,
  CardActions
} from '@mui/material';
import './App.css';

function App() {
  const [currentTab, setCurrentTab] = useState(0);
  const [apiResponse, setApiResponse] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const [packageId, setPackageId] = useState('');
  const [storingOrderId, setStoringOrderId] = useState('');
  const [airwayBillNumber, setAirwayBillNumber] = useState('');
  const [billOfEntryId, setBillOfEntryId] = useState('');
  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setCurrentTab(newValue);
  };

  const testApi = async (endpoint: string, method: string = 'GET', body?: any) => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetch(`https://kmoj7dnkpg.execute-api.us-east-2.amazonaws.com/Prod/${endpoint}`, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: body ? JSON.stringify(body) : undefined,
      });
      const data = await response.json();
      setApiResponse(data);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'API    .');
    } finally {
      setLoading(false);
    }
  };

  const renderApiTestForm = () => {
    switch (currentTab) {
      case 0: //  
        return (
          <Card>
            <CardContent>
              <MuiTypography variant="h6" gutterBottom>  API</MuiTypography>
              <MuiTypography variant="body2" color="text.secondary" paragraph>
                : GET /package
              </MuiTypography>
              <MuiTypography variant="body2" color="text.secondary" paragraph>
                : packageId ( )
              </MuiTypography>
              <MuiTypography variant="body2" color="text.secondary" paragraph>
                : /package?packageId=PACK1234
              </MuiTypography>
              <TextField
                fullWidth
                label="Package ID"
                value={packageId}
                onChange={(e) => setPackageId(e.target.value)}
                margin="normal"
                placeholder=": PACK1234"
              />
            </CardContent>
            <CardActions>
              <Button 
                variant="contained" 
                onClick={() => testApi(`package?packageId=${packageId}`)}
                disabled={!packageId}
              >
                 
              </Button>
            </CardActions>
          </Card>
        );
      case 1: //   
        return (
          <Card>
            <CardContent>
              <MuiTypography variant="h6" gutterBottom>   API</MuiTypography>
              <MuiTypography variant="body2" color="text.secondary" paragraph>
                : POST /storing-order/check
              </MuiTypography>
              <MuiTypography variant="body2" color="text.secondary" paragraph>
                (body): 
                - storingOrderId ()
                - airwayBillNumber ()
                - billOfEntryId ()
              </MuiTypography>
              <MuiTypography variant="body2" color="text.secondary" paragraph>
                 : 
                {`{
  "storingOrderId": "SO1234",
  "airwayBillNumber": "AWB0001",
  "billOfEntryId": "BOE0001"
}`}
              </MuiTypography>
              <TextField
                fullWidth
                label="Storing Order ID"
                value={storingOrderId}
                onChange={(e) => setStoringOrderId(e.target.value)}
                margin="normal"
                placeholder=": SO1234"
              />
              <TextField
                fullWidth
                label="Airway Bill Number"
                value={airwayBillNumber}
                onChange={(e) => setAirwayBillNumber(e.target.value)}
                margin="normal"
                placeholder=": AWB0001"
              />
              <TextField
                fullWidth
                label="Bill of Entry ID"
                value={billOfEntryId}
                onChange={(e) => setBillOfEntryId(e.target.value)}
                margin="normal"
                placeholder=": BOE0001"
              />
            </CardContent>
            <CardActions>
              <Button 
                variant="contained" 
                onClick={() => testApi('storing-order/check', 'POST', {
                  storingOrderId,
                  airwayBillNumber,
                  billOfEntryId
                })}
                disabled={!storingOrderId || !airwayBillNumber || !billOfEntryId}
              >
                  
              </Button>
            </CardActions>
          </Card>
        );
      case 2: //  API
        return (
          <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap' }}>
            <Card sx={{ flex: '1 1 300px' }}>
              <CardContent>
                <MuiTypography variant="h6" gutterBottom>  API</MuiTypography>
                <MuiTypography variant="body2" color="text.secondary" paragraph>
                  : GET /packages
                </MuiTypography>
                <MuiTypography variant="body2" color="text.secondary" paragraph>
                  : 
                </MuiTypography>
                <MuiTypography variant="body2" color="text.secondary" paragraph>
                     .
                </MuiTypography>
              </CardContent>
              <CardActions>
                <Button 
                  variant="contained" 
                  onClick={() => testApi('packages')}
                >
                    
                </Button>
              </CardActions>
            </Card>
            <Card sx={{ flex: '1 1 300px' }}>
              <CardContent>
                <MuiTypography variant="h6" gutterBottom>  API</MuiTypography>
                <MuiTypography variant="body2" color="text.secondary" paragraph>
                  : GET /pickslips
                </MuiTypography>
                <MuiTypography variant="body2" color="text.secondary" paragraph>
                  : 
                </MuiTypography>
                <MuiTypography variant="body2" color="text.secondary" paragraph>
                     .
                </MuiTypography>
              </CardContent>
              <CardActions>
                <Button 
                  variant="contained" 
                  onClick={() => testApi('pickslips')}
                >
                    
                </Button>
              </CardActions>
            </Card>
            <Card sx={{ flex: '1 1 300px' }}>
              <CardContent>
                <MuiTypography variant="h6" gutterBottom>   API</MuiTypography>
                <MuiTypography variant="body2" color="text.secondary" paragraph>
                  : GET /storing-orders
                </MuiTypography>
                <MuiTypography variant="body2" color="text.secondary" paragraph>
                  : 
                </MuiTypography>
                <MuiTypography variant="body2" color="text.secondary" paragraph>
                      .
                </MuiTypography>
              </CardContent>
              <CardActions>
                <Button 
                  variant="contained" 
                  onClick={() => testApi('storing-orders')}
                >
                     
                </Button>
              </CardActions>
            </Card>
          </Box>
        );
      default:
        return null;
    }
  };

  return (
    <div className="App">
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6">
            WMS API 
          </Typography>
        </Toolbar>
      </AppBar>
      
      <Container maxWidth="lg" className="App-main">
        <Paper elevation={3}>
          <Tabs
            value={currentTab}
            onChange={handleTabChange}
            indicatorColor="primary"
            textColor="primary"
            centered
          >
            <Tab label=" " />
            <Tab label="  " />
            <Tab label=" API" />
          </Tabs>
          
          <Box p={3}>
            {renderApiTestForm()}
            
            {loading && (
              <Box mt={3}>
                <MuiTypography> ...</MuiTypography>
              </Box>
            )}
            
            {error && (
              <Box mt={3}>
                <MuiTypography color="error">{error}</MuiTypography>
              </Box>
            )}
            
            {apiResponse && (
              <Box mt={3}>
                <MuiTypography variant="h6">API :</MuiTypography>
                <pre style={{ 
                  backgroundColor: '#f5f5f5', 
                  padding: '1rem', 
                  borderRadius: '4px',
                  overflow: 'auto'
                }}>
                  {JSON.stringify(apiResponse, null, 2)}
                </pre>
              </Box>
            )}
          </Box>
        </Paper>
      </Container>
    </div>
  );
}

export default App;
